
.footer {
    width: 100%;
    position: relative;
    padding: 2rem 0;
    display: flex;
    align-items: center;
    justify-content: center;
    margin: auto;
  }


  video {
    width: 100%;
    height: auto;
  }


  .footer .videoDiv {
    position: absolute;
    height: 100%;
    width: 100%;
  }
  .footer video {
    height: 100%;
    object-fit: cover;
  }
  .footer::after {
    content: "";
    position: absolute;
    background: rgba(10, 84, 107, 0.307);
    top: 0;
    bottom: 0;
    right: 0;
    left: 0;
    mix-blend-mode: multiply;
  }
  .footer .secContent {
    width: 100%;
    height: max-content;
    padding: 2rem initial;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    margin: auto;
    row-gap: 2rem;
    z-index: 100;
  }
  .footer .secContent .contactDiv {
    justify-content: space-between;
    flex-direction: column;
    gap: 1rem;
  }
  .footer .secContent .contactDiv .text small {
    font-size: 13px;
    font-weight: 400;
    color: var(--whiteColor);
  }
  .footer .secContent .contactDiv .text h2 {
    color: var(--whiteColor);
    font-size: 1.8rem;
  }
  .footer .secContent .contactDiv .inputDiv {
    width: 100%;
    gap: 1rem;
    flex-direction: column;
  }
  .footer .secContent .contactDiv .inputDiv input {
    width: 100%;
    padding: 0.5rem 0.6rem;
    border-radius: 3rem;
    border: 1px solid var(--whiteColor);
    outline: none;
    background: transparent;
    color: var(--whiteColor);
  }
  .footer .secContent .contactDiv .inputDiv input::placeholder {
    color: var(--whiteColor);
    opacity: 0.5;
  }
  .footer .secContent .contactDiv .inputDiv .btn {
    width: 100%;
    justify-content: center;
    color: var(--whiteColor);
    font-weight: 500;
    gap: 0.5rem;
  }
  .footer .secContent .contactDiv .inputDiv .btn .icon {
    font-size: 18px;
  }
  .footer .secContent .footerCard {
    position: relative;
    padding: 1rem 1rem 4rem;
    gap: 2rem;
    border-radius: 1rem;
    background: var(--bodyColor);
    width: 100%;
    justify-content: center;
    flex-direction: column;
    align-items: flex-start;
    overflow: hidden;
  }
  .footer .secContent .footerCard .footerIntro {
    flex-basis: 50%;
    justify-content: center;
    align-items: flex-start;
    flex-direction: column;
    row-gap: 0.5rem;
  }
  .footer .secContent .footerCard .logoDiv .logo {
    color: var(--blackColor);
    font-weight: 600;
  }
  .footer .secContent .footerCard .logoDiv .icon {
    font-size: 25px;
    color: var(--PrimaryColor);
    margin-top: 10px;
  }
  .footer .secContent .footerParagraph {
    font-size: 13px;
    color: var(--textColor);
  }
  .footer .secContent .footerSocials {
    column-gap: 0.5rem;
  }
  .footer .secContent .footerSocials .icon {
    color: var(--blackColor);
    font-size: 20px;
  }
  .footer .secContent .footerSocials .icon:hover {
    color: var(--PrimaryColor);
  }
  .footer .secContent .footerLinks {
    flex-basis: 50%;
    width: 100%;
    gap: 1rem;
    grid-template-columns: repeat(2, 1fr);
    justify-content: space-between;
  }
  .footer .secContent .footerLinks .linkGroup .groupTitle {
    display: block;
    font-weight: 600;
    color: var(--blackColor);
    margin-bottom: 0.5rem;
  }
  .footer .secContent .footerLinks .linkGroup .footerList {
    font-size: 15px;
    color: var(--textColor);
    transition: 0.3s ease-in-out;
  }
  .footer .secContent .footerLinks .linkGroup .footerList .icon {
    font-size: 15px;
    color: var(--grayText);
  }
  .footer .secContent .footerLinks .linkGroup .footerList:hover {
    color: var(--PrimaryColor);
    transform: translateX(5px);
  }
  .footer .secContent .footerLinks .linkGroup .footerList:hover .icon {
    color: var(--SecondaryColor);
  }
  .footer .secContent .footerDiv {
    flex-direction: column;
    position: absolute;
    bottom: 0;
    left: 0;
    right: 0;
    background: var(--SecondaryColor);
    padding: 0.5rem 2rem;
  }
  .footer .secContent .footerDiv small {
    font-size: 10px;
    color: var(--whiteColor);
  }
  
  @media screen and (min-width: 500px) {
    .footer .secContent .footerCard .footerLinks {
      grid-template-columns: repeat(3, 1fr);
      padding-bottom: 1rem;
    }
    .footer .secContent .footerCard .footerDiv {
      justify-content: space-between;
      flex-direction: row;
    }
    .footer .secContent .footerCard .footerDiv small {
      font-size: 14px;
      color: var(--whiteColor);
    }
  }
  @media screen and (min-width: 660px) {
    .footer .secContent .contactDiv {
      flex-direction: row;
      justify-content: space-between;
    }
    .footer .secContent .contactDiv .inputDiv {
      width: max-content;
      flex-direction: row;
      justify-content: flex-start;
    }
    .footer .secContent .contactDiv .inputDiv input {
      /* width: 60%; */
    }
    .footer .secContent .contactDiv .inputDiv .btn {
      width: max-content;
    }
  }
  @media screen and (min-width: 840px) {
    .footer {
      padding: 5rem;
    }
    .footer .secContent .footerCard {
      flex-direction: row;
    }
    .footer .secContent .footerCard .footerLinks {
      grid-template-columns: repeat(3, 1fr);
      padding-bottom: 1rem;
    }
  }
  @media screen and (min-width: 1024px) {
    .footer .secContent .footerCard {
      padding: 3rem 1rem;
    }
    .footer .secContent .footerCard .footerIntro .footerParagraph {
      font-size: 15px;
      color: var(--textColor);
    }
    .footer .secContent .footerCard .footerIntro .footerSocials {
      column-gap: 0.5rem;
    }
    .footer .secContent .footerCard .footerIntro .footerSocials .icon {
      color: var(--textColor);
      font-size: 25px;
    }
    .footer .secContent .footerCard .footerIntro .footerSocials .icon:hover {
      color: var(--PrimaryColor);
    }
    .footer .secContent .footerCard .footerLinks .linkGroup .footerList {
      font-size: 16px;
      padding: 5px 0;
    }
  }/*# sourceMappingURL=Footer.css.map */